import React, { FunctionComponent } from 'react'
import { StaticQuery, graphql } from 'gatsby'
import PostsList from 'partials/common/postsList';
import { formatPosts } from 'services/formaters'

/** 
 * QUery 
 */  
const query = graphql`
  query indexQuery {
    allMdx(
      sort: { fields: [frontmatter___date], order: ASC }
      filter: {fields: {group: {eq: "blog"}}, frontmatter: {publish: {eq: true}}}
      limit: 6
    ) {
      nodes {
        id
        frontmatter {
          title
          date
          category
          cover {
            childImageSharp {
              gatsbyImageData(layout: FULL_WIDTH)
            }
          }
        }
        fields {
          slug
          group
        }
        slug
        wordCount {
          words
        }
        excerpt(pruneLength: 200)
      }
    }
  }
`;

/**
 * View
 */
const HomeBlogPartial:FunctionComponent = () => (
  <StaticQuery query={query} render={( data:IAllMdxData ) => {
    let rawPosts = data?.allMdx?.nodes || null

    if (rawPosts && rawPosts.length < 1) return <></>

    return (
      <div className="container py-3 my-md-4"> 
        <h2 className="effect-square" id="anchor">
          Articles de blog
        </h2> 
        <PostsList posts={formatPosts(rawPosts)} />
      </div>
    )
  }} />
)

export default HomeBlogPartial
