import React, { FunctionComponent, useState, useEffect, useRef } from 'react'
import { animated, useSpring, config } from 'react-spring'
import { css } from "@emotion/css";

interface ITiltAndScroll {
  isSticky?: boolean,
  toBottom?: boolean,
  bgColor?: string,
  amplitude?: number
}

/**
 * Wrapper style
 * this element only allows to get the initial position in the rendered DOM
 */
const wrapperStyle = css`
  position: relative;
  z-index: 1;
  height: 0;
`

/**
 * Container style
 */
const containerStyle = (isSticky:boolean, stickyTopPosition:number|null) => css`
  ${isSticky && 'position: fixed;'}
  ${(isSticky && stickyTopPosition) && `top: ${stickyTopPosition}px;`}
  display: block;
  width: 100%;
  z-index: -1;
`
/**
 * SVG element style
 */
const svgStyle = css`
  display: block;
  top: 0;
  left: 0;
  width: 100%; 
  height: auto;
  stroke: none;
  overflow: visible;
`

/**
 * Helper
 * clamp between 0 and 1 then round with decimal
 */
const clampAndRoundValue = (v:number) => Math.round(Math.max(Math.min(v , 1), 0)* 10) / 10

const getSVGPoints = (value:number, toBottom = true) => toBottom ? `0,0 100,0 0,${value}` : `0,0 100,0 100,-${value}` 

/**
 * Tilt & Scroll
 * App
 */
const TiltAndScroll:FunctionComponent<ITiltAndScroll> = ({ 
  isSticky = false,
  toBottom = true,
  amplitude = 6,
  bgColor = 'var(--primary)' 
}) => {
  // value used in relationship with responsive viewport size
  // because we don't want the shape to get the same ratio on a small or a large screen
  const [viewportRectif, setViewportRectif] = useState<number>(0.75)

  // prevent unexpected situation
  const [isReady, setIsReady] = useState<boolean>(false)

  // store the sticky position when sticky mode
  const [stickyTopPosition, setStickyTopPosition] = useState<number|null>(null)

  // store last position to get the movement direction
  const lastPosition = useRef<number>(0)

  // Element ref
  const wrapRef = useRef<HTMLDivElement>(null)

  // Define spring animation 
  const [{ t }, api] = useSpring(() => ({ 
    from: { t: 0 },
    to: { t: 1 },
    delay: 0, 
    config: config.slow
  }))

  // Handle scroll movement 
  function handleScroll () {
    if (
      !wrapRef.current 
      || wrapRef.current.getBoundingClientRect().top > window.innerHeight
      || wrapRef.current.getBoundingClientRect().top < 0
    ) return
    

    let fraction = (1 / Math.min(wrapRef.current.getBoundingClientRect().top + window.scrollY, window.innerHeight))
    let portion = wrapRef.current.getBoundingClientRect().top - window.scrollY
    let transformValue = clampAndRoundValue(fraction * portion)

    // activate if necessary
    setIsReady(true)

    // movement direction 
    let isGoing = lastPosition.current < transformValue

    // update animation value
    api({ 
      t: transformValue, 
      delay: 800,
      config: {
        ...config.stiff,
        bounce: isGoing ? undefined : 0, 
        mass: isGoing ? 5 : 1
      } 
    }) 

    lastPosition.current = transformValue
  }
  
  const handleViewport = () => {
    let width = document.documentElement.offsetWidth
    setViewportRectif(width > 1500 ? 0.5 : width > 980 ? 0.7 : width > 920 ? 0.8 : width > 720 ? 0.85 : 1)
  }

  // Listen scroll event
  useEffect(() => {
    handleViewport()

    window.addEventListener('resize', handleViewport)
    window.addEventListener('scroll', handleScroll)
    return () => {
      window.removeEventListener('resize', handleViewport)
      window.removeEventListener('scroll', handleScroll)
    }
  }, [])
 
  // init sticky position
  // the delay limit the risk of a bad positionning on the page due of async rendering of elements around  
  useEffect(() => {
    let delay = setTimeout(() => {
      if (isSticky && !!wrapRef.current) {
        // calculate the initial position
        let stickyPos = Math.ceil(wrapRef.current.getBoundingClientRect().top + window.scrollY)
        
        // set the initial positon
        setStickyTopPosition(stickyPos)

        // validate initial render only if the element is visible
        if (window.scrollY < stickyPos) setIsReady(true)
      } else {
        setIsReady(true)
      }
    }, 300)

    return () => clearTimeout(delay)
  }, [wrapRef.current])

  // Render 
  return (
    <div ref={wrapRef} className={wrapperStyle}>
      {((isSticky && stickyTopPosition) || !isSticky) && isReady && (
        <div className={containerStyle(isSticky, stickyTopPosition)}> 
          <animated.svg 
            className={svgStyle}
            fill={bgColor}
            style={{visibility: t.to(t => t === 0 ? 'hidden' : 'visible')}}
            viewBox={t.to(t => `0 0 100 ${Math.max(t * amplitude * viewportRectif, 0)}`)} 
            preserveAspectRatio="xMinYMin slice" 
          >
            <animated.polygon points={t.to(t => getSVGPoints(t * amplitude * viewportRectif, toBottom))} /> 
          </animated.svg> 
        </div>
      )}
    </div>
  )
}  
  
export default TiltAndScroll 